.container-see-input {
    display: flex;
    justify-content: center;
    width: 100%;
    height: calc(1.5em + .75rem + 2px);
    padding: .375rem 0rem .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out; 

    @include general-media-o(min-width, $breakpoint-xlg-width, landscape) {
      height: auto;
    }
  }
  
  .see-input {
    width: 85%;
    border-width: 0px;
    border: none;

    @include media-width(xlg-width) {
      margin: .4rem 1rem;
    }

    @include media-width(xxxlg-width) {
      height: calc(1.5em + .75rem + 4px);
      padding: 3.375rem 2.75rem;
    }

    @include media-width(xxxxlg-width) {
      height: calc(1.5em + .75rem + 8px);
      padding: 6.9rem 4.75rem;
    }
  }
  
  .container-see-icon {
    display: flex;
    width: 15%;
    height: auto;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }

  .see-icon {
    @include general-range-media-o($breakpoint-min-nexus7, $breakpoint-max-nexus7, portrait){
      width: 40px;
    }

    @include media-width(sm-width) {
      width: 52px;
    }

    @include media-width(md-width) {
      width: 28px;
    }

    @include general-range-media-o($breakpoint-min-ipad-pro, $breakpoint-max-ipad-pro, portrait){
      width: 60px;
    }

    @include media-width(lg-width) {
      width: 30px;
    }

    @include media-width(mlg-width) {
      width: 42px;
    }

    @include media-width(xlg-width) {
      width: 56px;
    }

    @include media-width(xxxlg-width) {
      width: 84px;
    }

    @include media-width(xxxxlg-width) {
      width: 168px;
    }
  }