/* Background for home page */
.bg-home {
  background-image: url("/img/bg_home.png");
  background-position: center bottom;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  min-height: 100vh;

  @include general-media-o(min-width, 1025px, landscape){
    background-image: url("/img/bg_home_desktop.png");
  }

  h1 {
    color: $colorBlack80;
  }

  hr {
    @include media-width(lg-width) {
      height: 60px;
      border-top-width: 1.3px;
    }
  
    @include media-width(mlg-width) {
      height: 60px;
      border-top-width: 1.3px;
    }
  
    @include media-width(xlg-width) {
      height: 60px;
      border-top-width: 2px;
    }
  
    @include media-width(xxxlg-width) {
      height: 130px;
      border-top-width: 3px;
    }
  
    @include media-width(xxxxlg-width) {
      height: 260px;
      border-top-width: 6px;
    }
  }
}

.container {
  @include general-range-media-o($breakpoint-min-nexus7, $breakpoint-max-nexus7, portrait){
    max-width: 580px;
  }

  @include media-width(mlg-width) {
    max-width: 1500px;
  }

  @include media-width(xlg-width) {
    max-width: 2100px;
  }

  @include media-width(xxxlg-width) {
    max-width: 3500px;
  }

  @include media-width(xxxxlg-width) {
    max-width: 7000px;
  }
}

.flex-column {
  @include general-range-media-o($breakpoint-min-nexus7, $breakpoint-max-nexus7, portrait){
    height: 707px;
  }

  @include media-width(sm-width) {
    height: 760px;
  }

  @include media-width(md-width) {
    height: 576px;
  }

  @include general-range-media-o($breakpoint-min-ipad-pro, $breakpoint-max-ipad-pro, portrait){
    height: 910px;
  }

  @include media-width(mlg-width) {
    height: 760px;
  }

  @include media-width(xlg-width) {
    height: 980px;
  }

  @include media-width(xxxlg-width) {
    height: 1404px;
  }

  @include media-width(xxxxlg-width) {
    height: 2500px;
  }
}

.title-home {
  @include general-range-media-o($breakpoint-min-nexus7, $breakpoint-max-nexus7, portrait){
    font-size: 3.3rem;
  }

  @include media-width(sm-width) {
    font-size: 3.5rem;
  }

  @include media-width(md-width) {
    font-size: 2.8rem;
  }

  @include general-range-media-o($breakpoint-min-ipad-pro, $breakpoint-max-ipad-pro, portrait){
    font-size: 4.5rem;
  }

  @include media-width(lg-width) {
    font-size: 3rem;
  }

  @include media-width(mlg-width) {
    font-size: 4.2rem;
  }

  @include media-width(xlg-width) {
    font-size: 5.6rem;
  }

  @include media-width(xxxlg-width) {
    font-size: 8.4rem;
  }

  @include media-width(xxxxlg-width) {
    font-size: 16.8rem;
  }
}

.desc-home {
  @include general-range-media-o($breakpoint-min-nexus7, $breakpoint-max-nexus7, portrait){
    font-size: 2rem;
  }

  @include media-width(sm-width) {
    font-size: 2.2rem;
  }

  @include media-width(md-width) {
    font-size: 1.8rem;
  }

  @include general-range-media-o($breakpoint-min-ipad-pro, $breakpoint-max-ipad-pro, portrait){
    font-size: 2.9rem;
  }

  @include media-width(lg-width) {
    font-size: 2rem;
  }

  @include media-width(mlg-width) {
    font-size: 2.8rem;
  }

  @include media-width(xlg-width) {
    font-size: 3.7rem;
  }

  @include media-width(xxxlg-width) {
    font-size: 5.6rem;
  }

  @include media-width(xxxxlg-width) {
    font-size: 11.2rem;
  }
}

.container-btn-light {
  @include general-media-o(min-width, $breakpoint-min-nexus7, landscape) {
    display: flex;
    justify-content: center;
    width: 40%;
  }

  @include general-media-o(min-width, $breakpoint-min-nexus7, portrait) {
    display: flex;
    justify-content: center;
    width: 40%;
  }

  @include media-width(xxxlg-width) {
    margin-top: 3%;
  }

  @include media-width(xxxxlg-width) {
    margin-top: 3%;
  }
}

/* Button color definition */
.btn-light {
  @include mColor($primaryColor, $colorAliceBlue, $colorAliceBlue);

  @include general-media-o(min-width, $breakpoint-min-nexus7, landscape) {
    padding-left: 4%;
    padding-right: 4%;
  }

  @include general-media-o(min-width, $breakpoint-min-nexus7, portrait) {
    padding-left: 4%;
    padding-right: 4%;
  }
}

.btn-light:hover {
  @include mColor($primaryColor, $colorSolitude, $colorPattensBlue);
}

.btn-light:focus, .btn-light.focus {
  box-shadow: 0 0 0 0.2rem rgba(216, 217, 219, 0.5);
}

.btn-light.disabled, .btn-light:disabled {
  @include mColor($primaryColor, $colorAliceBlue, $colorAliceBlue);
}

.btn-light:not(:disabled):not(.disabled):active, .btn-light:not(:disabled):not(.disabled).active,
.show > .btn-light.dropdown-toggle {
  @include mColor($primaryColor, $colorPattensBlue, $colorLinkWater);
}

.btn-light:not(:disabled):not(.disabled):active:focus, .btn-light:not(:disabled):not(.disabled).active:focus,
.show > .btn-light.dropdown-toggle:focus {
  box-shadow: 0 0 0 0.2rem rgba(216, 217, 219, 0.5);
}