.container-menu-mobile {
    width: 100%;
    height: auto;
    margin-top: 7rem;

    @include general-media-o(min-width, 320px, portrait) {
        
        @include general-media(min-height, 568px) { // iphone 5s
            height: 430px;
        }
    }

    @include general-media-o(min-width, 360px, portrait) {
        
        @include general-media(min-height, 640px) { // Galaxy s5, Galaxy Note 3
            height: 500px;
        }
    }

    @include general-media-o(min-width, 375px, portrait) {
        
        @include general-media(min-height, 667px) { // iphone/6/7/8
            height: 530px;
        }

        @include general-media(min-height, 812px) { // iphone X
            height: 650px;
        }
    }

    @include general-media-o(min-width, 411px, portrait) {
        
        @include general-media(min-height, 731px) { // Pixel 2
            height: 580px;
        }

        @include general-media(min-height, 823px) { // Pixel 2 XL
            height: 670px;
        }
    }

    @include general-media-o(min-width, 600px, portrait) {
        
        @include general-media(min-height, 960px) { // Nexus 7
            height: 810px;
        }
    }

    @include general-media-o(min-width, 768px, portrait) {
                
        @include general-media(min-height, 1024px) { // iPad, iPad Mini
            height: 870px;
        }
    }

    @include general-media-o(min-width, 800px, portrait) {
        
        @include general-media(min-height, 1280px) { // Nexus 10
            height: 1100px;
        }
    }

    @include general-media-o(min-width, $breakpoint-md-width, portrait) {
        
        @include general-media(min-height, $breakpoint-lg-width) { // iPad Pro
            height: 1210px;
        }
    }


}

.container-menu-items {
    //background-color: gray;
    display: flex;
    justify-content: center;
    //align-items: center;
    flex-direction: column;
    height: 100%;

    .menu-item {
        display: flex;
        align-items: center;
        //background-color: green;
        height: auto;
        color: white;
        margin: 10px 0px;
    
        img {
            z-index: 1;
    
            @include general-media(min-width, 470px) {
                width: 127px;
            }
    
            @include general-range-media-o(530px, 535px, landscape) {
                margin-left: 3%;
            }
    
            @include general-media(min-width, 550px) {
                width: 180px;
            }
    
            @include general-media(min-width, 760px) {
                width: 205px;
            }
    
            @include general-media(min-width, 798px) {
                width: 210px;
            }
    
            @include general-media(min-width, 1023px) {
                width: 260px;
            }
            
        }
    
        .container-item {
            display: flex;
            justify-content: center;
            align-items: left;
            flex-direction: column;    
            width: 70%;
            height: auto;
            padding: 7px 0px;
            border-radius: 10px;
            position: absolute;
            margin-left: 20%; 
            
            @include general-media(min-width, 550px) {
                padding: 17px 11px;
            }
            
            @include general-media(min-width, 760px) {
                padding: 25px 14px;
            }
    
            @include general-media-o(min-width, 630px, landscape) {
                width: 65%;
                margin-left: 18%;
            }
    
            @include general-media-o(min-width, 730px, landscape) {
                width: 60%;
                margin-left: 16%;
            }
    
            @include general-media-o(min-width, 810px, landscape) {
                width: 65%;
                margin-left: 18%;
            }
    
            @include general-media-o(min-width, 960px, landscape) {
                width: 60%;
                margin-left: 16%;
            }
    
            @include general-media-o(min-width, 1023px, landscape) {
                width: 70%;
                margin-left: 20%;
            }
    
            h3 {
                //background-color: gray;
                font-size: 1.2rem;
                margin: 2px 10px;
                margin-left: 30%;
    
                @include general-media-o(min-width, 360px, portrait) {
                    margin-left: 25%;
                }
    
                @include general-media-o(min-width, 375px, portrait) {
                    font-size: 1.3rem;
                }
    
                @include general-media-o(min-width, 410px, portrait) {
                    margin-left: 20%;
                }
    
                @include general-media(min-width, 470px) {
                    margin-left: 15%;
                    font-size: 1.4rem;
                }
    
                @include general-media(min-width, 550px) {
                    margin-left: 20%;
                    font-size: 1.7rem;
                }
    
                @include general-media(min-width, 760px) {
                    margin-left: 15%;
                    font-size: 2rem;
                }
    
                @include general-media(min-width, 1023px) {
                    margin-left: 13%;
                    font-size: 2.5rem;
                }
    
            }
        
            p {
                //background-color: green;
                margin: 0;
                font-size: 0.78rem;
                margin: 0px 10px;
                margin-left: 30%;
    
                @include general-media-o(min-width, 360px, portrait) {
                    margin-left: 25%;
                }
    
                @include general-media-o(min-width, 375px, portrait) {
                    font-size: 0.9rem;
                }
    
                @include general-media-o(min-width, 410px, portrait) {
                    margin-left: 20%;
                }
    
                @include general-media(min-width, 470px) {
                    margin-left: 15%;
                    font-size: 1.05rem;
                }
    
                @include general-media(min-width, 550px) {
                    margin-left: 20%;
                    font-size: 1.3rem;
                }
    
                @include general-media(min-width, 760px) {
                    margin-left: 15%;
                    font-size: 1.5rem;
                }
    
                @include general-media(min-width, 1023px) {
                    margin-left: 13%;
                    font-size: 1.95rem;
                }            
            }
        }
    
        .bc-item-1 {
            background-color: $menuDashboardColor;
        }
    
        .bc-item-2 {
            background-color: $menuHistoryColor;
        }
    
        .bc-item-3 {
            background-color: $menuForecastColor;
        }
    }
    
} 


