/* Toggler modification to support X symbol */
.navbar-toggler>.close {
    display:inline;
}

.navbar-toggler.collapsed>.close, .navbar-toggler:not(.collapsed)>.navbar-toggler-icon {
    display:none;
}

/* Background for NavBar */
.bg-faoa {
    background-color: $navBarColor !important;

    .container-fluid {
        @include general-media(min-width, $breakpoint-xlg-width) {
            margin-top: 30px;
            margin-bottom: 30px;
            padding-right: 30px;
        }

        @include general-media(min-width, $breakpoint-xxxlg-width) {
            margin-top: 40px;
            margin-bottom: 40px;
            padding-right: 40px;
        }

        @include general-media(min-width, $breakpoint-xxxxlg-width) {
            margin-top: 80px;
            margin-bottom: 80px;
            padding-right: 80px;
        }
    }
    
    img {
        z-index: 1;

        @include general-media(min-width, $breakpoint-mlg-width) {
            width: 200px;
        }

        @include general-media(min-width, $breakpoint-xlg-width) {
            width: 300px;
        }

        @include general-media(min-width, $breakpoint-xxxlg-width) {
            width: 480px;
        }

        @include general-media(min-width, $breakpoint-xxxxlg-width) {
            width: 880px;
        }
    }

    .container-navbar-title {
        position: absolute;
        display: flex;
        justify-content: flex-end;
        flex-direction: row;
        align-items: center;
        width: 100%;
        z-index: -1;
        //background-color: green;

        .container-line-title{

            .line {
                width: 150px;
                height: 3px;
                background-color: $colorWhite;
                border-radius: 25px;
                margin-bottom: 4px;

                @include general-media(min-width, $breakpoint-xs-width) {
                    width: 169px;
                }

                @include general-media(min-width, 470px) {
                    width: 211px;
                }

                @include general-media(min-width, 568px) {
                    width: 266.25px;
                }

                @include general-media(min-width, 600px) {
                    width: 281.25px;
                }

                @include general-media(min-width, $breakpoint-sm-width) {
                    width: 360px;
                }

                @include general-media(min-width, $breakpoint-md-width) {
                    width: 400px;
                }

                @include general-media(min-width, $breakpoint-lg-width) {
                    width: 426.875px;
                }

                @include general-media(min-width, $breakpoint-mlg-width) {
                    width: 600px;
                    height: 4.2px;
                }

                @include general-media(min-width, $breakpoint-xlg-width) {
                    width: 800px;
                    height: 5.6px;
                }

                @include general-media(min-width, $breakpoint-xxxlg-width) {
                    width: 1200px;
                    height: 8.4px;
                }

                @include general-media(min-width, $breakpoint-xxxxlg-width) {
                    width: 2400px;
                    height: 16.7px;
                } 
                
            }
        }
    }
}

.bg-navbar-menu {
    background-color: $navBarColor !important;
}

.bg-navbar-dashboard {
    background-color: $menuDashboardColor !important;
}

.bg-navbar-history {
    background-color: $menuHistoryColor !important;
}

.bg-navbar-forecast {
    background-color: $menuForecastColor !important;
}

.navbar-font-color-black {
    color: $colorBlack;
}

.navbar-font-color-white {
    color: $colorWhite;
}

.bg {
    height: 100%;
    width: 100%;
}

.container-nav-link{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;

    .container-nav-item {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        
        .icon, .text {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center; 

            #ic-profile {
                @include general-media(min-width, $breakpoint-mlg-width) {
                    width: 28px;
                }
        
                @include general-media(min-width, $breakpoint-xlg-width) {
                    width: 37px;
                }
        
                @include general-media(min-width, $breakpoint-xxxlg-width) {
                    width: 56px;
                }
        
                @include general-media(min-width, $breakpoint-xxxxlg-width) {
                    width: 112px;
                }
            }
            
            #ic-logout {        
                @include general-media(min-width, $breakpoint-mlg-width) {
                    width: 42px;
                }
        
                @include general-media(min-width, $breakpoint-xlg-width) {
                    width: 56px;
                }
        
                @include general-media(min-width, $breakpoint-xxxlg-width) {
                    width: 84px;
                }
        
                @include general-media(min-width, $breakpoint-xxxxlg-width) {
                    width: 168px;
                }
            }
        }
        
        p {
            margin: 5px;
        }
    }
}

