.map {
    position: absolute;
    width: 100%;
    height: 100%;

    @include general-media(min-width, 320px) {
        margin-top: 12.2%;
        
        @include general-media(min-height, 480px) { // iphone 4
            height: 81.7%;
        }
        
        @include general-media(min-height, 568px) { // iphone 5s
            height: 84.7%;
        }
    }

    @include general-media(min-width, 360px) {
        margin-top: 11%;
        
        @include general-media(min-height, 640px) { // Galaxy s5, Galaxy Note 3
            height: 86%;
        }
    }

    @include general-media(min-width, 375px) {
        margin-top: 11%;
        
        @include general-media(min-height, 667px) { // iphone/6/7/8
            height: 86.4%;
        }

        @include general-media(min-height, 812px) { // iphone X
            height: 88.8%;
        }
    }

    @include general-media(min-width, 411px) {
        margin-top: 10%;
        
        @include general-media(min-height, 731px) { // Pixel 2
            height: 87.5%;
        }

        @include general-media(min-height, 823px) { // Pixel 2 XL
            height: 88.9%; 
        }
    }

    @include general-media-o(min-width, 480px, landscape) {
        margin-top: 8.2%;
        
        @include general-media(min-height, 320px) { // iphone 4
            height: 72.5%;
        }
    }

    @include general-media-o(min-width, 568px, landscape) { // iphone 5s
        margin-top: 7%; 
    }

    @include general-media(min-width, 600px) {
        margin-top: 7%;
        
        @include general-media(min-height, 960px) { // Nexus 7
            height: 90.5%;
        }
    }

    @include general-media-o(min-width, 640px, landscape) {
        margin-top: 6.2%;
        
        @include general-media(min-height, 360px) { // Galaxy s5, Galaxy Note 3
            height: 75.5%;
        }
    }

    @include general-media-o(min-width, 731px, landscape) {
        margin-top: 5.3%;

        @include general-media(min-height, 411px) { // Pixel 2
            height: 78.7%;
        }
    }

    @include general-media(min-width, 768px) {
        margin-top: 5.2%;
        
        @include general-media(min-height, 1024px) { // iPad, iPad Mini
            height: 91.5%;
        }
    }

    @include general-media(min-width, 800px) {
        margin-top: 5%;
        
        @include general-media(min-height, 1280px) { // Nexus 10
            height: 93%;
        }
    }

    @include general-media-o(min-width, 823px, landscape) {
        margin-top: 4.7%;

        @include general-media(min-height, 411px) { // Pixel 2 XL
            height: 78.7%;
        }
    }

    @include general-media-o(min-width, 960px, landscape) {
        margin-top: 4.2%;

        @include general-media(min-height, 600px) { // Nexus 7
            height: 85.5%;
        }
    }

    @include general-media(min-width, $breakpoint-md-width) {
        margin-top: 4%;

        @include general-media(min-height, $breakpoint-lg-width) { // iPad Pro
            height: 93.5%;
        }
    }

    @include general-media-o(min-width, $breakpoint-md-width, landscape) {
        margin-top: 4%;

        @include general-media(min-height, 768px) { // iPad, iPad Mini
            height: 88.5%;
        }
    }

    @include general-media(min-width, 1280px) {
        margin-top: 3.2%;

        @include general-media(min-height, 720px) {
            height: 87.5%;
        }

        @include general-media(min-height, 800px) {
            height: 88.5%;
        }

        @include general-media(min-height, 950px) {
            height: 90.5%;
        }
    }

    @include general-media(min-width, $breakpoint-lg-width) {
        margin-top: 3%;

        @include general-media(min-height, $breakpoint-md-width) { // iPad Pro landscape
            height: 91.5%;
        }
    }

    @include general-media(min-width, $breakpoint-mlg-width) {
        margin-top: 2.8%;
        height: 90%;
    }

    @include general-media(min-width, $breakpoint-xlg-width) {
        margin-top: 5.7%;
        height: 86%;
    }

    @include general-media(min-width, $breakpoint-xxxlg-width) {
        margin-top: 5.9%;
        height: 86.5%;
    }

    @include general-media(min-width, $breakpoint-xxxxlg-width) {
        margin-top: 5.7%;
        height: 88%;
    }
}