// variables:
$breakpoint-xs-width: 360px;
$breakpoint-sm-width: 767px;
$breakpoint-md-width: 1023px;
$breakpoint-lg-width: 1360px;
$breakpoint-mlg-width: 1760px;
$breakpoint-xlg-width: 2420px;
$breakpoint-xxlg-width: 3020px;
$breakpoint-xxxlg-width: 3520px; // 4k
$breakpoint-xxxxlg-width: 7020px; // 8k
// other
$breakpoint-min-ipad-pro: 1024px;
$breakpoint-max-ipad-pro: 1120px;
$breakpoint-min-nexus7: 600px;
$breakpoint-max-nexus7: 700px;

@mixin media-width($media) {
    @if $media == xs-width {
      @media (max-width: $breakpoint-xs-width) { @content; }
    }
    @else if $media == sm-width {
      @media (min-width: $breakpoint-sm-width) and (max-width: $breakpoint-md-width) { @content; }
    }
    @else if $media == md-width {
      @media (min-width: $breakpoint-md-width) and (max-width: $breakpoint-lg-width) { @content; }
    }
    @else if $media == lg-width {
      @media (min-width: $breakpoint-lg-width) and (max-width: $breakpoint-mlg-width) { @content; }
    }
    @else if $media == mlg-width {
      @media (min-width: $breakpoint-mlg-width) and (max-width: $breakpoint-xlg-width) { @content; }
    }
    @else if $media == xlg-width {
      @media (min-width: $breakpoint-xlg-width) and (max-width: $breakpoint-xxlg-width) { @content; }
    }
    @else if $media == xxlg-width {
      @media (min-width: $breakpoint-xxlg-width) and (max-width: $breakpoint-xxxlg-width) { @content; }
    }
    @else if $media == xxxlg-width {
      @media (min-width: $breakpoint-xxxlg-width) and (max-width: $breakpoint-xxxxlg-width) { @content; }
    }
    @else if $media == xxxxlg-width {
      @media (min-width: $breakpoint-xxxxlg-width) { @content; }
    }
}

@mixin general-media($width_or_height, $breakpoint) {
  @media ($width_or_height: $breakpoint) { @content; }
}

@mixin general-media-o($width_or_height, $breakpoint, $orientation) {
  @media ($width_or_height: $breakpoint) and (orientation: $orientation) { @content; }
}

@mixin general-range-media($breakpoint1, $breakpoint2) {
  @media (min-width: $breakpoint1) and (max-width: $breakpoint2) { @content; }
}

@mixin general-range-media-o($breakpoint1, $breakpoint2, $orientation) {
  @media (min-width: $breakpoint1) and (max-width: $breakpoint2) and (orientation: $orientation) { @content; }
}

@mixin t($fontSize, $lineHeight) {
    font-size: $fontSize;
    line-height: $lineHeight;
}

@mixin mColor($color, $backgroundColor, $borderColor) {
    color: $color;
    background-color: $backgroundColor;
    border-color: $borderColor;
}