.container-forecast {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: auto;

    @include general-media(min-width, 1280px) {
        
        @include general-media(min-height, 720px) {
            height: 580px;
        }

        @include general-media(min-height, 800px) {
            height: 660px;
        }

        @include general-media(min-height, 950px) {
            height: 810px;
        }
    }

    @include general-media(min-width, $breakpoint-lg-width) {

        @include general-media(min-height, 600px) { 
            height: 460px;
        }

        @include general-media(min-height, 720px) {
            height: 610px;
        }
        
        @include general-media(min-height, $breakpoint-md-width) { // iPad Pro landscape
            height: 880px;
        }
    }

    @include general-media(min-width, 1440px) {
        
        @include general-media(min-height, 890px) {
            height: 750px;
        }
    }

    @include general-media(min-width, $breakpoint-mlg-width) {

        @include general-media(min-height, 800px) {
            height: 675px;
        }

        @include general-media(min-height, 910px) {
            height: 785px;
        }

        @include general-media(min-height, 1060px) {
            height: 955px;
        }
    }

    @include general-media(min-width, $breakpoint-xlg-width) {
        margin-top: 10rem!important;

        @include general-media(min-height, 1400px) {
            height: 1170px;
        }
    }

    @include general-media(min-width, $breakpoint-xxxlg-width) {
        margin-top: 16rem!important;

        @include general-media(min-height, 1900px) {
            height: 1520px;
        }

        @include general-media(min-height, 2100px) {
            height: 1780px;
        }
    }

    @include general-media(min-width, $breakpoint-xxxxlg-width) {
        margin-top: 30rem!important;

        @include general-media(min-height, 4000px) {
            height: 3380px;
        }

        @include general-media(min-height, 4300px) {
            height: 3690px;
        }
    }
}


.title-fore {
    color: $colorZambezi;
    margin-top: 80px !important;
    margin-bottom: 50px !important;

    @include general-media(min-width, $breakpoint-xlg-width) {
        margin-bottom: 66.6px !important;
    }
    
    @include general-media(min-width, $breakpoint-xxxlg-width) {
        margin-bottom: 100px !important;
    }
  
    @include general-media(min-width, $breakpoint-xxxxlg-width) {
        margin-bottom: 200px !important;
    }

    h5 {
        line-height: 0.7;
        font-weight: lighter;
    }
}

.parent-item-fore {
    display: flex; 
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 50px;
    width: 100%;
    
    .item-fore {
        width: 80%;
        margin-bottom: 30px;

        @include general-media(min-width, $breakpoint-xlg-width) {
            margin-bottom: 40px;
        }
        
        @include general-media(min-width, $breakpoint-xxxlg-width) {
            margin-bottom: 60px;
        }
      
        @include general-media(min-width, $breakpoint-xxxxlg-width) {
            margin-bottom: 120px;
        }
        
        .container-title {
            position: relative;
            border-top-left-radius: 7px;
            border-top-right-radius: 7px;
            background-color: $menuForecastColor;
            padding: 3px;

            h5 {
                color: $colorWhite;
                margin: 10px;

                @include general-media(min-width, $breakpoint-xlg-width) {
                    margin: 13.3px;
                }
                
                @include general-media(min-width, $breakpoint-xxxlg-width) {
                    margin: 20px;
                }
              
                @include general-media(min-width, $breakpoint-xxxxlg-width) {
                    margin: 40px;
                }
            }

            .triangle {
                width: 0; 
                height: 0; 
                border-left: 12px solid $menuForecastColor;
                border-top: 10px solid transparent;
                border-bottom: 10px solid transparent;
                position: absolute;
                right: 20px;
                bottom: -16px;

                -webkit-transform: rotate(90deg);
                -moz-transform: rotate(90deg);
                -ms-transform: rotate(90deg);
                -o-transform: rotate(90deg);
                transform: rotate(90deg);

                @include general-media(min-width, $breakpoint-xlg-width) {
                    border-left-width: 16px;
                    border-top-width: 13.3px;
                    border-bottom-width: 13.3px;
                    right: 26.6px;
                    bottom: -21px;
                }
                
                @include general-media(min-width, $breakpoint-xxxlg-width) {
                    border-left-width: 24px;
                    border-top-width: 20px;
                    border-bottom-width: 20px;
                    right: 40px;
                    bottom: -32px;
                }
              
                @include general-media(min-width, $breakpoint-xxxxlg-width) {
                    border-left-width: 48px;
                    border-top-width: 40px;
                    border-bottom-width: 40px;
                    right: 80px;
                    bottom: -64px;
                }
            }
        }

        .container-desc {
            width: 100%;
            height: 100%;
            border: 1px solid $colorDarkGray;
            border-bottom-left-radius: 7px;
            border-bottom-right-radius: 7px;

            @include general-media(min-width, $breakpoint-xlg-width) {
				border-width: 1.3px;
			}
			
			@include general-media(min-width, $breakpoint-xxxlg-width) {
				border-width: 2px;
			}
		  
			@include general-media(min-width, $breakpoint-xxxxlg-width) {
				border-width: 4px;
			}

            p {
                color: $colorZambezi;
                width: 85%;
                margin: 10px;

                @include general-media(min-width, $breakpoint-xlg-width) {
                    margin: 13.3px;
                }
                
                @include general-media(min-width, $breakpoint-xxxlg-width) {
                    margin: 20px;
                }
              
                @include general-media(min-width, $breakpoint-xxxxlg-width) {
                    margin: 40px;
                }
            }
        }
    }
}

.fore-color-gray {
    color: $colorZambezi;
    font-weight: bold;
}

.fore-color-red {
    color: $colorAmaranth;
    font-weight: bold;
}

.fore-color-green {
    color: $colorAppleGreen;
    font-weight: bold;
}

.fore-color-skyBlue {
    color: $colorSkyBlue;
    font-weight: bold;
}
