/* Background for login page */
.bg-login {
  background: url("/img/bg_login.png") center right no-repeat fixed;
  background-size: cover;

  @include general-media-o(min-width, 1025px, landscape){
    background-image: url("/img/bg_login_desktop.png");
    background-position: center bottom;
  }
}

/* Sizing form for flex container */
.form-signin {
  width: 100%;
  max-width: 420px;
}

/* Modal for filling user data in first login */
.c-modal {
  /* some styles to position the modal at the center of the page */
  position: fixed;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  text-align: center;
  overflow: hidden;

  /* needed styles for the overlay */
  z-index: 10; /* keep on top of other elements on the page */
  outline: 0;
}

/* Button color definition */
.btn-success {
  @include mColor($loginBtnFontColor, $primaryColor, $primaryColor);
}

.btn-success:hover {
  @include mColor($loginBtnFontColor, $primaryColor, $primaryColor);
}

.btn-success:focus, .btn-success.focus {
  box-shadow: 0 0 0 0.2rem rgba(72, 180, 97, 0.1);
}

.btn-success.disabled, .btn-success:disabled {
  @include mColor($loginBtnFontColor, $primaryColor, $primaryColor);
}

.btn-success:not(:disabled):not(.disabled):active, .btn-success:not(:disabled):not(.disabled).active,
.show > .btn-success.dropdown-toggle {
  @include mColor($loginBtnFontColor, $primaryColor, $primaryColor);
}

.btn-success:not(:disabled):not(.disabled):active:focus, .btn-success:not(:disabled):not(.disabled).active:focus,
.show > .btn-success.dropdown-toggle:focus {
  box-shadow: 0 0 0 0.2rem rgba(72, 180, 97, 0.5);
}