.container-alert {
    display: flex;
    justify-content: center;
}

/* Alert color definition */
.alert-danger {
    @include mColor($alertFontColor, $alertColor, $alertColor);
    display: flex;
    justify-content: center;
    padding: .75rem 1rem;

    @include general-media-o(min-width, $breakpoint-xlg-width, landscape) {
        padding: 1.75rem 1rem;
    }
}
  
.alert-danger hr {
    border-top-color: $alertColor;
}
  
.alert-danger .alert-link {
    color: $alertFontColor;
}

.container-warning-icon {
    display: flex;
    width: 20%;
    height: auto;
    justify-content: center;
    align-items: center;
}

.warning-icon {
    @include general-range-media-o($breakpoint-min-nexus7, $breakpoint-max-nexus7, portrait){
        width: 60px;
    }

    @include media-width(sm-width) {
        width: 75px;
    }

    @include media-width(md-width) {
        width: 67px;
    }

    @include general-range-media-o($breakpoint-min-ipad-pro, $breakpoint-max-ipad-pro, portrait){
        width: 80px;
    }

    @include media-width(lg-width) {
        width: 71.5px;
    }

    @include media-width(mlg-width) {
        width: 100.5px;
    }

    @include media-width(xlg-width) {
        width: 134px;
    }

    @include media-width(xxxlg-width) {
        width: 201px;
    }

    @include media-width(xxxxlg-width) {
        width: 402px;
    }
}

.container-warning-message {
    display: flex;
    width: 80%;
    height: auto;
    justify-content: center;
    align-items: center;
    margin-left: 10px;

    @include media-width(lg-width) {
        margin-left: 10.7px;
    }

    @include media-width(mlg-width) {
        margin-left: 15px;
    }

    @include media-width(xlg-width) {
        margin-left: 20px;
    }

    @include media-width(xxxlg-width) {
        margin-left: 30px;
    }

    @include media-width(xxxxlg-width) {
        margin-left: 60px;
    }
}

