.modal-title-dash {

    @include media-width(xlg-width) {
        margin-left: 0.3rem !important;
    }
  
    @include media-width(xxxlg-width) {
        margin-left: 1.2rem !important;
    }
  
    @include media-width(xxxxlg-width) {
        margin-left: 2rem !important;
    }
}

.input-group-dash {
    margin-top: 20px;
    margin-bottom: 20px;
  
    @include general-media(min-width, $breakpoint-md-width) {
        width: 30vw;
    }
  
    @include general-media(min-width, 1200px) {
        width: 30%;
    }
    
    @include general-media(min-width, $breakpoint-xxxlg-width) {
        margin-top: 40px;
        margin-bottom: 40px;
    }
  
    @include general-media(min-width, $breakpoint-xxxxlg-width) {
        margin-top: 80px;
        margin-bottom: 80px;
    }
}