.overlay {
	position: fixed;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background-color: $notFoundOverlayColor;
	z-index: -1;

	.my-5 {

		@include general-media(min-width, $breakpoint-mlg-width) {
			margin-top: 3.7rem !important;
    		margin-bottom: 3.7rem !important;
		}

		@include general-media(min-width, $breakpoint-xlg-width) {
			margin-top: 7rem !important;
    		margin-bottom: 7rem !important;
		}
		
		@include general-media(min-width, $breakpoint-xxxlg-width) {
			margin-top: 14rem !important;
    		margin-bottom: 14rem !important;
		}
	  
		@include general-media(min-width, $breakpoint-xxxxlg-width) {
			margin-top: 34rem !important;
    		margin-bottom: 34rem !important;
		}
	}
}

.c{
	text-align: center;
	display: block;
	position: relative;
	width: 80%;
	margin: 15% auto;
	z-index: 0;
	color: $notFoundFontColor;
}

.container-notFound {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 0;
	color: $notFoundFontColor;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;

	.flex-column {
		display: flex;
		flex-direction: column;
		align-items: center;
    	justify-content: center;
    	width: 80%;
		height: 100%;

		.display-1 {
			@include general-media(min-width, $breakpoint-xlg-width) {
				font-size: 8rem;
				margin-bottom: 0.67rem;
			}
			
			@include general-media(min-width, $breakpoint-xxxlg-width) {
				font-size: 12rem;
				margin-bottom: 1rem;
			}
		  
			@include general-media(min-width, $breakpoint-xxxxlg-width) {
				font-size: 24rem;
				margin-bottom: 2rem;
			}
		}

		.h1 {
			@include general-media(min-width, $breakpoint-xlg-width) {
				font-size: 3.33rem;
				margin-bottom: 0.67rem;
			}
			
			@include general-media(min-width, $breakpoint-xxxlg-width) {
				font-size: 5rem;
				margin-bottom: 1rem;
			}
		  
			@include general-media(min-width, $breakpoint-xxxxlg-width) {
				font-size: 10rem;
				margin-bottom: 2rem;
			}
		}

		.h2 {
			@include general-media(min-width, $breakpoint-xlg-width) {
				font-size: 2.7rem;
				margin-bottom: 0.67rem;
			}
			
			@include general-media(min-width, $breakpoint-xxxlg-width) {
				font-size: 4rem;
				margin-bottom: 1rem;
			}
		  
			@include general-media(min-width, $breakpoint-xxxxlg-width) {
				font-size: 8rem;
				margin-bottom: 2rem;
			} 
		}
	}
}

/* KEYFRAMES */

@-webkit-keyframes animateCloud {
	0% {
		margin-left: -1000px;
	}

	100% {
		margin-left: 100%;
	}
}

@-moz-keyframes animateCloud {
	0% {
		margin-left: -1000px;
	}

	100% {
		margin-left: 100%;
	}
}

@keyframes animateCloud {
	0% {
		margin-left: -1000px;
	}

	100% {
		margin-left: 100%;
	}
}

/* ANIMATIONS */

.x1 {
	-webkit-animation: animateCloud 35s linear infinite;
	-moz-animation: animateCloud 35s linear infinite;
	animation: animateCloud 35s linear infinite;

	-webkit-transform: scale(0.65);
	-moz-transform: scale(0.65);
	transform: scale(0.65);

	@include general-media(min-width, $breakpoint-xlg-width) {
		-webkit-transform: scale(0.86);
		-moz-transform: scale(0.86);
		transform: scale(0.86);
