.menu-desktop {
    margin-top: 9rem!important;
    height: auto;

    @include general-media(min-width, 1280px) {
        
        @include general-media(min-height, 720px) {
            height: 520px;
        }

        @include general-media(min-height, 800px) {
            height: 550px;
        }

        @include general-media(min-height, 950px) {
            height: 630px;
        }
    }

    @include general-media(min-width, $breakpoint-lg-width) {

        @include general-media(min-height, 600px) { 
            height: 450px;
        }

        @include general-media(min-height, 720px) {
            height: 520px;
        }
        
        @include general-media(min-height, $breakpoint-md-width) { // iPad Pro landscape
            height: 760px;
        }
    }

    @include general-media(min-width, 1440px) {
        
        @include general-media(min-height, 890px) {
            height: 650px;
        }
    }

    @include general-media(min-width, $breakpoint-mlg-width) {
        margin-top: 13rem!important;
    }

    @include general-media(min-width, $breakpoint-xlg-width) {
        margin-top: 20rem!important;

        @include general-media(min-height, 1400px) {
            height: 940px;
        }
    }

    @include general-media(min-width, $breakpoint-xxxlg-width) {
        margin-top: 30rem!important;

        @include general-media(min-height, 1900px) {
            height: 1090px;
        }

        @include general-media(min-height, 2100px) {
            height: 1380px;
        }
    }

    @include general-media(min-width, $breakpoint-xxxxlg-width) {
        margin-top: 60rem!important;

        @include general-media(min-height, 4000px) {
            height: 2300px;
        }

        @include general-media(min-height, 4300px) {
            height: 2810px;
        }
    }

    img {
        margin-bottom: 20px;
        cursor: pointer;

        @include general-media(min-width, $breakpoint-mlg-width) {
            width: 210px;
            margin-bottom: 30px;
        }

        @include general-media(min-width, $breakpoint-xlg-width) {
            width: 281px;
            margin-bottom: 40px;
        }

        @include general-media(min-width, $breakpoint-xxxlg-width) {
            width: 422px;
            margin-bottom: 60px;
        }

        @include general-media(min-width, $breakpoint-xxxxlg-width) {
            width: 843px;
            margin-bottom: 120px;
        }
    }

    h2 {
        @include general-media(min-width, $breakpoint-mlg-width) {
            margin-bottom: 1rem;
        }

        @include general-media(min-width, $breakpoint-xlg-width) {
            margin-bottom: 2rem;
        }

        @include general-media(min-width, $breakpoint-xxxlg-width) {
            margin-bottom: 4rem;
        }

        @include general-media(min-width, $breakpoint-xxxxlg-width) {
            margin-bottom: 8rem;
        }

    }

    p {
        @include general-media(min-width, $breakpoint-mlg-width) {
            margin-bottom: 1.5rem;
        }

        @include general-media(min-width, $breakpoint-xlg-width) {
            margin-bottom: 2.5rem;
        }

        @include general-media(min-width, $breakpoint-xxxlg-width) {
            margin-bottom: 4.5rem;
        }

        @include general-media(min-width, $breakpoint-xxxxlg-width) {
            margin-bottom: 8.5rem;
        }
    }
}