.container-profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: auto;
    margin-top: 20px;
    margin-bottom: 20px;

    .title {
        padding-top: 10px;
        padding-right: 15px;
        padding-left: 15px;
    }

    .parent {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 100%;
        margin-bottom: 10px;

        @include general-media(min-width, $breakpoint-xlg-width) {
            margin-bottom: 13.33px;
        } 
    
        @include general-media(min-width, $breakpoint-xxxlg-width) {
            margin-bottom: 20px;
        }
    
        @include general-media(min-width, $breakpoint-xxxxlg-width) {
            margin-bottom: 40px;
        }

        .container-icon {
            width: 25%;
            text-align: center;

            .icon {
                width: 55%;
                padding: 5px;

                @include general-media-o(min-width, 480px, landscape) {
        
                    @include general-media(min-height, 320px) {
                        width: 35%;
                    }
                }

                @include general-media-o(min-width, 810px, landscape) {
        
                    @include general-media(min-height, 370px) {
                        width: 40%;
                    }
                }

                @include general-media-o(min-width, 1024px, landscape) {
        
                    @include general-media(min-height, 768px) {
                        width: 35%;
                    }
                }

                @include general-media(min-width, $breakpoint-mlg-width) {
                    width: 45%;
                }

            }
        }

        .container-text {
            width: 75%;

            span {
                padding: .375rem .25rem;
            }
        }        
    }
}

