.container-loading{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1072;

    .container-spinner{
        display: flex;
        flex-direction: column;
        justify-content: center; /* center items vertically */
        align-items: center;     /* center items horizontally */
        width: 100%;
        height: 100%;

        .loader{
            // Mobile First
            border: 7px solid $colorWhite10;
            border-radius: 200%;
            border-top: 7px solid $colorWhite50;
            width: 12vw;
            height: 12vw;
            -webkit-animation: spin 1s linear infinite; /* Safari */
            animation: spin 1s linear infinite;
            
            @include general-media(min-width, 600px) {
                border-width: 14px;
                border-top-width: 14px;
            }

            @include general-media(min-width, $breakpoint-sm-width) {
                width: 10vw;
                height: 10vw;
            }

            @include general-media-o(min-width, 450px, landscape) {
                border-width: 8px;
                border-top-width: 8px;
                width: 8vw;
                height: 8vw;
            }

            @include general-media-o(min-width, 600px, landscape) {
                width: 7vw;
                height: 7vw;
            }

            @include general-media-o(min-width, 700px, landscape) {
                width: 6vw;
                height: 6vw;
            }

            // De aqu en adelante desktop y tv
            @include general-media(min-width, 1200px) {
                border-width: 10px;
                border-top-width: 10px;
                width: 5vw;
                height: 5vw;
            }

            @include general-media(min-width, $breakpoint-mlg-width) {
                border-width: 13px;
                border-top-width: 13px;
            }

            @include general-media(min-width, $breakpoint-xlg-width) {
                border-width: 16px;
                border-top-width: 16px;
            }

            @include general-media(min-width, $breakpoint-xxxlg-width) {
                border-width: 25px;
                border-top-width: 25px;
            }

            @include general-media(min-width, $breakpoint-xxxxlg-width) {
                border-width: 50px;
                border-top-width: 50px;
            }
            
        }
    }
}

.bg-color-black-50 {
    background-color: $colorBlack50; 
}

/* Safari */
@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
}
  
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}