.col-auto-hist {

    @include general-media(min-width, $breakpoint-xlg-width) {
        margin-top: 8rem;
        margin-left: 1rem;
    }

    @include general-media(min-width, $breakpoint-xxxlg-width) {
        margin-top: 14rem;
        margin-left: 2rem;
    }

    @include general-media(min-width, $breakpoint-xxxxlg-width) {
        margin-top: 28rem;
        margin-left: 4rem;
    }
}

.btn-hist {
    background-color: $colorVioletBlue;
    border-color: $colorVioletBlue;

    @include general-media(min-width, $breakpoint-xlg-width) {
        padding: 17.5px;
    }

    @include general-media(min-width, $breakpoint-xxxlg-width) {
        padding: 33.7px;
    }

    @include general-media(min-width, $breakpoint-xxxxlg-width) {
        padding: 53.7px;
    }
    
}

.btn-hist:hover {
    background-color: $colorVioletBlue;
    border-color: $colorVioletBlue;
}

