/*
** COMMON CLASSES **
*/

/* width */
::-webkit-scrollbar {
  width: 3px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.hg{
  height: auto !important;
}

.hg-vh{
  height: 100vh;

  @include general-media-o(max-height, 650px, landscape) {
    height: auto;
  }
  
  @include general-media-o(min-width, 1025px, landscape) {
    height: 100vh;
  }
}

.logo {
  @include general-range-media-o($breakpoint-min-nexus7, $breakpoint-max-nexus7, portrait){
    width: 470px;
  }

  @include media-width(sm-width) {
    width: 500px;
  }

  @include media-width(md-width) {
    width: 309.2px;
  }

  @include general-range-media-o($breakpoint-min-ipad-pro, $breakpoint-max-ipad-pro, portrait){
    width: 670px;
  }

  @include media-width(lg-width) {
    width: 330px;
  }

  @include media-width(mlg-width) {
    width: 463.8px;
  }

  @include media-width(xlg-width) {
    width: 618.4px;
  }

  @include media-width(xxxlg-width) {
    width: 927.6px;
  }

  @include media-width(xxxxlg-width) {
    width: 1855.2px;
  }
}

.font-size-res-0 {
  $r0: 0.2rem;

  @include general-range-media-o($breakpoint-min-nexus7, $breakpoint-max-nexus7, portrait){
      font-size: 1.35rem - $r0;
  }

  @include media-width(sm-width) {
      font-size: 1.7rem - $r0;
  }

  @include media-width(md-width) {
    font-size: 1rem - $r0;
  }

  @include general-range-media-o($breakpoint-min-ipad-pro, $breakpoint-max-ipad-pro, portrait){
      font-size: 1.9rem - $r0;
  }

  @include media-width(lg-width) {
    font-size: 0.85rem;
  }

  @include media-width(mlg-width) {
      font-size: 1.2rem;
  }

  @include media-width(xlg-width) {
      font-size: 1.6rem;
  }

  @include media-width(xxxlg-width) {
      font-size: 2.4rem;
  }

  @include media-width(xxxxlg-width) {
    font-size: 4.8rem;
  }
}

.font-size-res-1 {
  @include general-range-media-o($breakpoint-min-nexus7, $breakpoint-max-nexus7, portrait){
      font-size: 1.35rem;
  }

  @include media-width(sm-width) {
      font-size: 1.7rem;
  }

  /* @include media-width(md-width) {
    font-size: 1rem;
  } */

  @include general-range-media-o($breakpoint-min-ipad-pro, $breakpoint-max-ipad-pro, portrait){
      font-size: 1.9rem;
  }

  @include media-width(lg-width) {
    font-size: 1.07rem;
  }

  @include media-width(mlg-width) {
      font-size: 1.5rem;
  }

  @include media-width(xlg-width) {
      font-size: 2rem;
  }

  @include media-width(xxxlg-width) {
      font-size: 3rem;
  }

  @include media-width(xxxxlg-width) {
    font-size: 6rem;
  }
}

.font-size-res-2 {
  $r2: 0.25rem;

  @include general-range-media-o($breakpoint-min-nexus7, $breakpoint-max-nexus7, portrait){
      font-size: 1.35rem + $r2;
  }

  @include media-width(sm-width) {
      font-size: 1.7rem + $r2;
  }

  @include general-range-media-o($breakpoint-min-ipad-pro, $breakpoint-max-ipad-pro, portrait){
      font-size: 1.9rem + $r2;
  }

  @include media-width(lg-width) {
    font-size: 1.33rem;
  }

  @include media-width(mlg-width) {
      font-size: 1.87rem;
  }

  @include media-width(xlg-width) {
      font-size: 2.5rem;
  }

  @include media-width(xxxlg-width) {
      font-size: 3.75rem;
  }

  @include media-width(xxxxlg-width) {
    font-size: 7.5rem;
  }
}

.font-size-res-3 {
  $r3: 0.75rem;

  @include general-range-media-o($breakpoint-min-nexus7, $breakpoint-max-nexus7, portrait){
      font-size: 1.35rem + $r3;
  }

  @include media-width(sm-width) {
      font-size: 1.7rem + $r3;
  }

  @include general-range-media-o($breakpoint-min-ipad-pro, $breakpoint-max-ipad-pro, portrait){
      font-size: 1.9rem + $r3;
  }

  @include media-width(lg-width) {
    font-size: 1.87rem;
  }

  @include media-width(mlg-width) {
      font-size: 2.62rem;
  }

  @include media-width(xlg-width) {
    font-size: 3.5rem;
  }

  @include media-width(xxxlg-width) {
    font-size: 5.25rem;
  }

  @include media-width(xxxxlg-width) {
    font-size: 10.5rem;
  }
}

.font-size-res-btn {
  @include general-range-media-o($breakpoint-min-nexus7, $breakpoint-max-nexus7, portrait){
    font-size: 1.4rem;
  }

  @include media-width(sm-width) {
    font-size: 1.77rem;
  }

  @include media-width(md-width) {
    font-size: 1.07rem;
  }

  @include general-range-media-o($breakpoint-min-ipad-pro, $breakpoint-max-ipad-pro, portrait){
    font-size: 2rem;
  }

  @include media-width(lg-width) {
    font-size: 1.14rem;
  }

  
  @include media-width(mlg-width) {
    font-size: 1.6rem;
  }

  @include media-width(xlg-width) {
    font-size: 2.14rem;
    padding: 1rem;
  }

  @include media-width(xxxlg-width) {
    font-size: 3.21rem;
    padding: 1.3rem;
  }

  @include media-width(xxxxlg-width) {
    font-size: 6.42rem;
    padding: 1.7rem;
  }
}

.modal-dialog-res {
  @include general-media-o(min-width, $breakpoint-min-nexus7, portrait) {
    max-width: 89%;
  }

  @include general-media-o(min-width, $breakpoint-min-nexus7, landscape) {
    max-width: 89%;
  }

  @include general-media-o(min-width, 630px, portrait) {
    max-width: 63%;
  }

  @include general-media-o(min-width, 630px, landscape) {
    max-width: 63%;
  }

  @include general-media-o(min-width, 750px, portrait) {
    max-width: 83%;
  }

  @include general-media-o(min-width, 750px, landscape) {
    max-width: 83%;
  }

  @include general-media-o(min-width, $breakpoint-md-width, portrait) {
    max-width: 44%;
  }

  @include general-media-o(min-width, $breakpoint-md-width, landscape) {
    max-width: 33%;
  }

  @include general-range-media-o($breakpoint-min-ipad-pro, $breakpoint-max-ipad-pro, portrait){
    max-width: 70%;
  }

  @include general-range-media-o($breakpoint-min-ipad-pro, $breakpoint-max-ipad-pro, landscape){
    max-width: 55%;
  }

}

.modal-content-res {
  @include general-media-o(min-width, 400px, portrait) {
    padding: 20px;
  }

  @include general-media-o(min-width, 400px, landscape) {
    padding: 20px;
  }

  @include media-width(mlg-width) {
    padding: 40px;
  }

  @include media-width(xlg-width) {
    padding: 50px;
  }

  @include media-width(xxxlg-width) {
    padding: 70px;
  }

  @include media-width(xxxxlg-width) {
    padding: 140px;
  }
}


.form-group {
  @include media-width(xlg-width) {
    margin-bottom: 2.4rem;
  }

  @include media-width(xxxlg-width) {
    margin-bottom: 3.4rem;
  }

  @include media-width(xxxxlg-width) {
    margin-bottom: 8rem;
  }
}

.form-control {
  @include media-width(xlg-width) {
    padding: 2.3rem 1rem;
  }

  @include media-width(xxxlg-width) {
    height: calc(1.5em + .75rem + 4px);
    padding: 3.9rem 2.75rem;
  }

  @include media-width(xxxxlg-width) {
    height: calc(1.5em + .75rem + 8px);
    padding: 6.9rem 4.75rem;
  }
}

.form-control-select {
    display: block;
    width: 100%;
    height: calc(1.5em + .75rem + 2px);
    padding: .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;

    option{
      font-size: 1rem;
    }

    $r0: 0.2rem;

    @include general-range-media-o($breakpoint-min-nexus7, $breakpoint-max-nexus7, portrait){
        font-size: 1.35rem - $r0;
    }

    @include media-width(sm-width) {
        font-size: 1.7rem - $r0;
    }

    @include media-width(md-width) {
      font-size: 1rem - $r0;
    }

    @include general-range-media-o($breakpoint-min-ipad-pro, $breakpoint-max-ipad-pro, portrait){
        font-size: 1.9rem - $r0;
    }

    @include media-width(lg-width) {
      font-size: 0.85rem;
    }

    @include media-width(mlg-width) {
        font-size: 1.2rem;
    }

    @include media-width(xlg-width) {
      height: calc(1.5em + .75rem + 26px);
        font-size: 1.6rem;
    }

    @include media-width(xxxlg-width) {
      height: calc(1.5em + .75rem + 57px);
        font-size: 2.4rem;
    }

    @include media-width(xxxxlg-width) {
      height: calc(1.5em + .75rem + 97px);
      font-size: 4.8rem;
    }
}

.mb-3, .my-3 {
  @include media-width(xlg-width) {
    margin-bottom: 2rem!important;
  }

  @include media-width(xxxlg-width) {
    margin-bottom: 4rem!important;
  }

  @include media-width(xxxxlg-width) {
    margin-bottom: 8rem!important;
  }
}

.modal-body {
  @include media-width(xlg-width) {
    margin-bottom: 1rem!important;
  }

  @include media-width(xxxlg-width) {
    padding: 3rem;
  }

  @include media-width(xxxxlg-width) {
    padding: 6rem;
  }
}

.m-modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  display: block;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0;
}

.m-modal-show {
  display: block;
}

.m-modal-hide {
  display: none;
}

.container-dialog {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.modal-dialog {

  @include general-media-o(min-width, 1024px, portrait) {
    max-width: 600px;
  }

  @include general-media(min-width, $breakpoint-xlg-width) {
    max-width: 750px;
  }

  @include general-media(min-width, $breakpoint-xxxlg-width) {
    max-width: 1024px;
  }

  @include general-media(min-width, $breakpoint-xxxxlg-width) {
    max-width: 2048px;
  }
}

.m-dialog {
  height: 100%;
}

.modal-close {
  @include media-width(xlg-width) {
    font-size: 2.5rem;
  }

  @include media-width(xxxlg-width) {
    margin-right: 0.2rem !important;
    font-size: 4.5rem;
  }

  @include media-width(xxxxlg-width) {
    margin-right: 1rem !important;
    font-size: 6.5rem;
  }
}

.modal-xl {
  @include media-width(xlg-width) {
    max-width: 1440px;
  }

  @include media-width(xxxlg-width) {
    max-width: 2160px;
  }

  @include media-width(xxxxlg-width) {
    max-width: 4320px;
  }
}